/*------------------------------------*\
  #Element Paragraph
\*------------------------------------*/

p {
  font-family: 'Lato', sans-serif;
  font-weight: $font-weight-light;
  line-height: 1.4em;
  font-size:   1em;
}

@include media(">tablet") {
  p { font-size:   0.9em; }
}

p a {
  text-decoration: underline;
}